footer {
    font-size: 0.8rem;
    justify-content: center;
    align-items: center;
    position: relative;
    display: block;
    width: 100%;
    a {
        color: var(--fg);
    }

    > svg {
        &.triangle {
            fill: var(--bg);
            -moz-transform: translateZ(0);
            -webkit-transform: translateZ(0);
            transform: translateZ(0);
            width: 100%;
            min-height: 200px;
            margin-left: -1px;
        }
    }
    > #footer-background {
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        background-attachment: fixed;
        background-size: 180px 180px;
        background-image: url("/img/pattern.png");
        background-repeat: repeat;
        background-color: var(--color-magnolia);
    }
    > #footer-content {
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        > .info {
            position: absolute;
            right: 0;
            bottom: 0;
            padding: 0 3rem 0 0;
            -moz-box-sizing: border-box;
            -webkit-box-sizing: border-box;
            box-sizing: border-box;
            text-align: right;
            p {
                margin: 0;
            }
            > .social-bar {
                margin-bottom: 0.8rem;
            }
        }
    }
}

@media only screen and (max-width: 768px) {
    footer {
        > #footer-content {
            > .info {
                padding-right: 1rem;
            }
        }
    }
}
